import { Component, OnInit } from '@angular/core';
import { WebService } from '../../services/web.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-select-trainer',
  templateUrl: './select-trainer.component.html',
  styleUrls: ['./select-trainer.component.css']
})
export class SelectTrainerComponent implements OnInit {

  public trainers;
  public trainers_arr = [];

  constructor(private _webSrv: WebService,
              private _router: Router) { }

  ngOnInit() {
    this.trainers = this._webSrv.getTrainers();
    this.trainers.subscribe(trainers => {
      var trainserObj = JSON.parse(trainers['_body']);
      this.trainers_arr = [];
      for(var key in trainserObj) {
        this.trainers_arr.push(trainserObj[key]);
      }

      console.log(this.trainers_arr);
      this._webSrv.trainers_arr = this.trainers_arr;
    })
  }

  selectTrainer(trainer) {
    console.log(trainer);
    // var some = this._webSrv.selectTrainerCall(trainer);
    this._router.navigate(['select-date']);
  }
}



