import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { SelectTrainerComponent } from './routeComponents/select-trainer/select-trainer.component';
import { SelectDateComponent } from './routeComponents/select-date/select-date.component';
import { SelectTimeComponent } from './routeComponents/select-time/select-time.component';
import { InputDetailsComponent } from './routeComponents/input-details/input-details.component';
import { ConfirmationComponent } from './routeComponents/confirmation/confirmation.component';

const routes: Routes = [
    {   path: '',
        redirectTo: '/select-a-trainer',
        pathMatch: 'full' },
    { path: 'select-a-trainer', component: SelectTrainerComponent },
    { path: 'select-date', component: SelectDateComponent },
    { path: 'select-time', component: SelectTimeComponent },
    { path: 'input-details', component: InputDetailsComponent },
    { path: 'confirmation', component: ConfirmationComponent },
  ];
  
  @NgModule({
    imports: [RouterModule.forRoot(routes)],
    exports: [RouterModule],
    providers: []
  })
  export class AppRoutingModule { }


