import { Injectable } from '@angular/core';
import { Http } from '@angular/http';

import 'rxjs/add/operator/map';

@Injectable()
export class WebService {

  public trainers_arr = null;
  public selected_date = null;
  public selected_trainer_key = null;
  public selected_trainer_name = null;
  public trainer_schedule = null;
  public selected_slot = null;

  public submissionResponse = null;

  constructor(private _http: Http) { }

  selectTrainerCall(trainer) {
    return "Saved trainer " + trainer;
  }

  getTrainers() {
    return this._http.get('https://us-central1-remindly-dev.cloudfunctions.net/getTrainers')
      .map(res => res);
      
  }

  getTrainerScheduleTillEOM(trainer_key, week) {
    console.log(trainer_key, 'sending this');
    return this._http.get('https://us-central1-remindly-dev.cloudfunctions.net/get_schedule_till_end_of_month?trainer_key=' + trainer_key + '&week=' + week)
      .map(res => res);
  }

  submitAnAppointment(appointmentObj) {
    return this._http.post('https://us-central1-remindly-dev.cloudfunctions.net/submitAppointment', appointmentObj)
      .map(res => res);
  }
}



