import { Component, OnInit } from '@angular/core';
import {Router, ActivatedRoute, Params} from '@angular/router';
import { WebService } from '../../services/web.service';

@Component({
  selector: 'app-schedule',
  templateUrl: './select-date.component.html',
  styleUrls: ['./select-date.component.css']
})
export class SelectDateComponent implements OnInit {

  public trainer;
  public trainer_key;
  public trainer_name; 

  public month = 'September';
  public year = '2017';

  public week1 = [];
  public week2 = [];
  public week3 = [];
  public week4 = [];
  public week5 = [];
  public week6 = [];


  constructor(private _activeRoute: ActivatedRoute,
              private _router: Router,
              private _webSrv: WebService) { }

  ngOnInit() {
    this._activeRoute.queryParams.subscribe((params: Params) => {
      console.log(params);
      this.trainer_key = params['trainer_key'];
      this._webSrv.selected_trainer_key = this.trainer_key;
      this.trainer_name = params['trainer_name'];
      this._webSrv.selected_trainer_name = this.trainer_name;

      if(this.trainer_key == undefined) {
        this._router.navigate(['select-a-trainer']);
      } else {
        this._webSrv.getTrainerScheduleTillEOM(this.trainer_key, 0)
          .subscribe(response => {
            console.log(response);
            console.log(JSON.parse(response['_body']));
  
            this.updateSchedule(JSON.parse(response['_body']));
          });
      }
    })
  }

  updateSchedule(trainer_schedule) {
    this._webSrv.trainer_schedule = trainer_schedule;

    var today = new Date();
    var todayDate = today.getDate();
    console.log(todayDate);
    var startDateIndex = trainer_schedule['first_day'];
    var endDateIndex = trainer_schedule['last_day'];

    var scheduleArray = [];
    var date = 1;

    // create an array w/ 42 elems
    for (var i = 0; i < 42; i++) {
      var start = false;

      if(i >= startDateIndex) {
        start = true;
      } else {scheduleArray.push({date: "N/A", show: false, available: false})}

      if(start && date <= endDateIndex) {
        var tempObj = {
          date: date,
          show: true,
          available: false,
          isToday: false
        }

        if(trainer_schedule[date] && trainer_schedule[date]['availability'] != false) {
          tempObj.available = true;
        }
        if(date == todayDate) {
          tempObj.isToday = true;
        }

        scheduleArray.push(tempObj);
        date++;
      }
    }

    for(var i = 0; i < 7; i++) {
        this.week1.push(scheduleArray[i])
    }
    console.log(this.week1);
    for(var i = 7; i < 14; i++) {
      if(scheduleArray[i]['show']) {
        this.week2.push(scheduleArray[i])
      }
    }
    console.log(this.week2);
    for(var i = 14; i < 21; i++) {
      if(scheduleArray[i]['show']) {
        this.week3.push(scheduleArray[i])
      }
    }
    console.log(this.week3);
    for(var i = 21; i < 28; i++) {
      if(scheduleArray[i]['show']) {
        this.week4.push(scheduleArray[i])
      }
    }
    console.log(this.week4);
    for(var i = 28; i < 35; i++) {
      if(scheduleArray[i]['show']) {
        this.week5.push(scheduleArray[i])
      }
    }
    console.log(this.week5);
    for(var i = 35; i < 42; i++) {
      if(scheduleArray[i] && scheduleArray[i]['show']) {
        this.week6.push(scheduleArray[i])
      }
    }
    console.log(this.week6);

    // console.log(scheduleArray);



  }

  dateSelect(available_boolean, selected_date) {
    if(available_boolean) {
      this._webSrv.selected_date = selected_date;
      this._router.navigate(['select-time']);
    } else {
      alert("This date is not available");
    }
  }

  // HELPERs 

  get_sched_helper_1(day_num) {
    switch(day_num) {
      case 0:
        return 'sunday';
      case 1:
        return 'monday';
      case 2:
        return 'tuesday';
      case 3:
        return 'wednesday';
      case 4:
        return 'thursday';
      case 5:
        return 'friday';
      case 6:
        return 'saturday';

      default:
        return false;
    }
  }
}



