import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';

import { HttpModule } from '@angular/http';

import { AppComponent } from './app.component';
import { SelectTrainerComponent } from './routeComponents/select-trainer/select-trainer.component';

import { AppRoutingModule } from './app.routing';
import { SelectDateComponent } from './routeComponents/select-date/select-date.component';

import { WebService } from './services/web.service';
import { SelectTimeComponent } from './routeComponents/select-time/select-time.component';
import { InputDetailsComponent } from './routeComponents/input-details/input-details.component';
import { ConfirmationComponent } from './routeComponents/confirmation/confirmation.component';

@NgModule({
  declarations: [
    AppComponent,
    SelectTrainerComponent,
    SelectDateComponent,
    SelectTimeComponent,
    InputDetailsComponent,
    ConfirmationComponent
  ],
  imports: [
    BrowserModule,
    HttpModule,
    AppRoutingModule,
    FormsModule,
    ReactiveFormsModule,
  ],
  providers: [
    WebService
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



